.bg {
  /* background: url(https://i.ibb.co/87GbbFP/2799006.jpg) no-repeat; */
  /* background: url('../Assets/BG.jpg') no-repeat; */
  background: #222325 no-repeat;
  background-position: center;
  background-size: cover;
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: -3;
  /* -webkit-animation: bg 12s ease-out both;
	        animation: bg 12s ease-out both; */
}
.bg:before {
  content: "";
  width: 100%;
  height: 100%;
  background: #000;
  position: absolute;
  z-index: -10;
  top: 0;
  left: 0;
  opacity: 0.3;
}
@-webkit-keyframes sf-fly-by-1 {
  from {
    -webkit-transform: translateZ(-600px);
            transform: translateZ(-600px);
    opacity: 0.5;
  }
  to {
    -webkit-transform: translateZ(0);
            transform: translateZ(0);
    opacity: 0.5;
  }
}
@keyframes sf-fly-by-1 {
  from {
    -webkit-transform: translateZ(-600px);
            transform: translateZ(-600px);
    opacity: 0.5;
  }
  to {
    -webkit-transform: translateZ(0);
            transform: translateZ(0);
    opacity: 0.5;
  }
}
@-webkit-keyframes sf-fly-by-2 {
  from {
    -webkit-transform: translateZ(-1200px);
            transform: translateZ(-1200px);
    opacity: 0.5;
  }
  to {
    -webkit-transform: translateZ(-600px);
            transform: translateZ(-600px);
    opacity: 0.5;
  }
}
@keyframes sf-fly-by-2 {
  from {
    -webkit-transform: translateZ(-1200px);
            transform: translateZ(-1200px);
    opacity: 0.5;
  }
  to {
    -webkit-transform: translateZ(-600px);
            transform: translateZ(-600px);
    opacity: 0.5;
  }
}
@-webkit-keyframes sf-fly-by-3 {
  from {
    -webkit-transform: translateZ(-1800px);
            transform: translateZ(-1800px);
    opacity: 0.5;
  }
  to {
    -webkit-transform: translateZ(-1200px);
            transform: translateZ(-1200px);
    opacity: 0.5;
  }
}
@keyframes sf-fly-by-3 {
  from {
    -webkit-transform: translateZ(-1800px);
            transform: translateZ(-1800px);
    opacity: 0.5;
  }
  to {
    -webkit-transform: translateZ(-1200px);
            transform: translateZ(-1200px);
    opacity: 0.5;
  }
}
